<template>
  <v-hover v-slot:default="{ hover }">
    <v-card
            class="mx-auto pad-card"
            style="border-radius: 10px; padding-top: 10px"
            color="grey lighten-4"
            width="130"
            height="150"
    >
    <div style="display: table-cell;vertical-align: middle;">
      <v-img
              class="text-center"
              style="margin: auto"
              contain
              height="72"
              width="72"
              :src="info.scr"
      >
        <v-expand-transition>
          <div
                  v-if="hover"
                  class="grey lighten-4  v-card--reveal black--text "
                  style="height: 100%"
          >
            <familiars-stats :stats="info.stats"/>
          </div>
        </v-expand-transition>
      </v-img>

      <v-card-text v-if="!hover" class="text-under-card">
        <div>
          {{info.name}}
        </div>
      </v-card-text>

      <v-card-text v-if="hover" class="text-under-card" @click="overlay = !overlay">
        <v-btn outlined depressed small block>
          <v-icon>mdi-account-details</v-icon>
        </v-btn>
      </v-card-text>

      <v-dialog :value="overlay" persistent width="400">
        <v-card>
          <div class="d-flex flex-no-wrap justify-space-between">
            <div>
              <v-card-title :class="`headline ${getRarity()[0]}`">{{info.name}}</v-card-title>

              <v-card-subtitle>
                <div>{{getRarity()[1]}} фамильяр</div>
                <div>{{info.where}}</div>
                <div>{{info.bonus}}</div>
              </v-card-subtitle>

            </div>
            <div style="width: 60px;margin-top: 16px;margin-right: -18px">
              <familiars-stats :stats="info.stats"/>
            </div>
            <v-avatar
                    class="ma-3"
                    size="130"
                    tile
            >
              <v-img
                     contain
                     :src="info.scr">
              </v-img>
            </v-avatar>
          </div>
          <v-row style="margin: auto">
            <div v-for="(v, index) in info.skills" :key="index" style="margin: auto">
              <FamiliarsSkills :skills="v" style="margin-bottom: 15px"/>
            </div>
          </v-row>


            <v-card-actions>
              <v-btn text color="deep-purple" @click="overlay = false">
                Закрыть
              </v-btn>
            </v-card-actions>
        </v-card>
      </v-dialog>

    </div>
    </v-card>
  </v-hover>
