</template>

<script>
  import FamiliarsStats from "./FamiliarsStats";
  import FamiliarsSkills from "./FamiliarsSkills";
    export default {
        name: "fam-page",
        components: {
            FamiliarsStats,
            FamiliarsSkills
        },
        props: {
            info: Object,
            rarity: Number
        },
        data: () => ({
            overlay: false
        }),
        methods: {
            getRarity(){
                if (this.rarity === 0){
                    return ["green--text", "Обычный"]
                } else if (this.rarity === 1){
                    return ["blue--text", "Редкий"]
                } else if (this.rarity === 2){
                    return ["red--text", "Эпический"]
                }else if (this.rarity === 3){
                    return ["yellow--text  text--darken-1", "Легендарный"]
                }else if (this.rarity === 4){
                    return ["pink--text", "Мифический"]
                }else {
                    return ["green--text", "Обычный"]
                }
            }
        },
    }
</script>

<style>

  .text-under-card{
    position: relative;
    font-size: 0.85em;
    height: 54px;
    min-width: 90px;
  }

  .pad-card{
    /*padding-bottom: 5px;*/
    /*padding-top: 5px;*/
    display: table;
  }
</style>