<template>
  <v-hover v-slot:default="{ hover }">
    <v-card
            class="mx-auto pad-card"
            color="grey lighten-4"
            height="70"
            max-width="150"
            min-width="110"
            style="border-radius: 10px; padding-top: 10px"
    >
      <div style="display: table-cell;vertical-align: middle;">
        <v-img
                :src="info.scr"
                class="text-center"
                contain
                height="65"
                style="margin: auto"
                width="65"
        >
          <v-expand-transition>
            <div
                    class="grey lighten-4  v-card--reveal black--text "
                    style="height: 100%"
                    v-if="hover"
            >
              <familiars-stats :stats="info.stats"/>
            </div>
          </v-expand-transition>
        </v-img>

        <v-card-text class="text-under-card" v-if="!hover">
          <div>
            {{info.name}}
          </div>
        </v-card-text>

        <v-card-text @click="overlay = !overlay" class="text-under-card" v-if="hover">
          <v-btn block depressed outlined small>
            <v-icon>mdi-account-details</v-icon>
          </v-btn>
        </v-card-text>

        <v-dialog :value="overlay" persistent max-width="400">
          <v-card>
            <div class="d-flex flex-no-wrap justify-space-between">
              <div>
                <v-card-title :class="`headline ${getRarity(info)[0]} text--darken-1`">{{info.name}}</v-card-title>

                <v-card-subtitle>
                  <div>{{getRarity(info)[1]}} фамильяр</div>
                </v-card-subtitle>

              </div>
              <div style="width: 60px;margin-top: 16px;margin-right: -18px">
                <familiars-stats :stats="info.stats"/>
              </div>
              <v-avatar
                      class="ma-3"
                      size="130"
                      tile
              >
                <v-img
                        :src="info.scr"
                        contain>
                </v-img>
              </v-avatar>
            </div>
            <v-row style="margin: auto">
              <div :key="index" style="margin: auto" v-for="(v, index) in info.skills">
                <FamiliarsSkills :skills="v"/>
              </div>
            </v-row>

            <v-subheader>Собирается из:</v-subheader>
            <v-row style="margin: auto">
              <div v-for="(value, i) in info.where" :key="i" style="margin: auto;" :class="`${getRarity(value)[0]} text--darken-2`">
                <div>
                  <div>
                    <v-card-title :class="`headline ${getRarity(value)[0]} text--darken-1`">{{value.name}}</v-card-title>

                    <v-card-subtitle>
                      <div>{{getRarity(value)[1]}} фамильяр</div>
                      <div>z3d4</div>
                    </v-card-subtitle>

                  </div>
<!--                  <div style="width: 60px;margin-top: 16px;margin-right: -18px">-->
<!--                    <familiars-stats :stats="value.stats"/>-->
<!--                  </div>-->
                  <v-avatar
                          class="ma-3"
                          size="130"
                          tile
                  >
                    <v-img
                            contain
                            :src="value.scr">
                    </v-img>
                  </v-avatar>
                </div>
                <!--                  <v-row style="margin: auto">-->
                <!--                    <div v-for="(skill, index) in value.skills" :key="index" style="margin: auto">-->
                <!--                      <FamiliarsSkills :skills="skill"/>-->
                <!--                    </div>-->
                <!--                  </v-row>-->
              </div>
            </v-row>

            <v-card-actions>
              <v-btn @click="overlay = false" color="deep-purple" text>
                Закрыть
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </div>
    </v-card>

  </v-hover>
</template>

<script>
    import FamiliarsStats from "./FamiliarsStats";
    import FamiliarsSkills from "./FamiliarsSkills";

    export default {
        name: "fam-page",
        components: {
            FamiliarsStats,
            FamiliarsSkills
        },
        props: {
            info: Object,
        },
        data: () => ({
            overlay: false
        }),
        methods: {
            getRarity(fam) {
                if (fam.rarity === 0) {
                    return ["green--text", "Обычный"]
                } else if (fam.rarity === 1) {
                    return ["blue--text", "Редкий"]
                } else if (fam.rarity === 2) {
                    return ["red--text", "Эпический"]
                } else if (fam.rarity === 3) {
                    return ["yellow--text", "Легендарный"]
                } else if (fam.rarity === 4) {
                    return ["pink--text", "Мифический"]
                } else {
                    return ["green--text", "Обычный ???"]
                }
            },
            // getWhere(){
            //     for (let x in this.info.where){
            //         console.log(this.info.where);
            //     }
            // }
        },
    }
</script>

<style>

  .text-under-card {
    position: relative;
    font-size: 0.85em;
    height: 54px;
    min-width: 90px;
  }

  .pad-card {
    /*padding-bottom: 5px;*/
    /*padding-top: 5px;*/
    display: table;
  }
</style>