<template>
  <div>
    <v-row style="margin-left: auto; padding: 1px; float: left"
           v-for="(index, v) in stats" :key="counter(v)[0]">
        <v-img contain
               height="19" width="19"
               :src="counter(v)[1]"/>
        <div style="font-size: 0.85em;margin-left: 7px">{{index}}%</div>
    </v-row>
  </div>
</template>

<script>
export default {
    name: "FamiliarsStats",
    props: {
        stats: Object
    },
    data: () => ({
        ScrPower: "https://vignette.wikia.nocookie.net/bit-heroes/images/a/a8/UIPower.png/revision/latest?cb=20161026152414",
        ScrHealth: "https://vignette.wikia.nocookie.net/bit-heroes/images/b/b7/UIStamina.png/revision/latest?cb=20161026152422",
        ScrSpeed: "https://vignette.wikia.nocookie.net/bit-heroes/images/6/63/UIAgility.png/revision/latest?cb=20161026152407",
    }),
    methods: {
        counter(v){
            if (v === "power"){
                return [0, this.ScrPower]
            } else if (v === "health"){
                return [1, this.ScrHealth]
            } else {
                return [2, this.ScrSpeed]
            }
        }
    }
}
</script>

