import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import Familiars from '../views/Familiars.vue'
import AdminPanel from '../views/AdminPanel.vue'
Vue.use(VueRouter);

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: About
  },
  {
    path: '/familiars',
    name: 'Familiars',
    component: Familiars
  },
  {
    path: '/admin',
    name: 'Admin',
    component: AdminPanel
  }
];

const router = new VueRouter({
  routes
});

export default router
