<template>
  <v-app>
    <Menu/>
    <v-tabs grow
            v-model="tab"
    >

      <v-tabs-slider/>
      <v-tab>
        Основные
      </v-tab>

      <v-tab-item>
        <v-expansion-panels accordion multiple>
          <v-expansion-panel
                  :key="i"
                  v-for="(item,i) in fam"
          >
            <v-expansion-panel-header :class="`${getRarity(i)[0]} text--darken-1`">{{getRarity(i)[1]}}
            </v-expansion-panel-header>
            <v-expansion-panel-content>
              <v-row>
                <v-col cols="12">
                  <v-row>
                    <v-col :key="index"
                           class="text-center"
                           style="margin: auto"
                           v-for="(v, index) in item">
                      <main-fam-block :info="v" :rarity="i"/>
                    </v-col>
                  </v-row>
                </v-col>
              </v-row>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>

      </v-tab-item>

      <v-tabs-slider/>

      <v-tab>
        Слияние
      </v-tab>

      <v-tab-item>
        <v-row>
          <v-col cols="12">
            <v-card>
              <v-container fluid>
                <v-row>
                  <v-col
                          :key="index"
                          class="text-center"
                          v-for="(v, index) in fam_fusion">
                    <fusion-fam-block :info="v"/>
                  </v-col>
                </v-row>
              </v-container>
            </v-card>
          </v-col>
        </v-row>
      </v-tab-item>
    </v-tabs>
  </v-app>
</template>

<script>
    import MainFamBlock from "../components/Familiars/MainFamiliarsBlock"
    import FusionFamBlock from "../components/Familiars/FusionFamiliarsBlock";

    export default {
        name: 'Familiars',
        components: {
            Menu: () => import('../components/menu.vue'),
            MainFamBlock,
            FusionFamBlock
        },
        data: () => ({
            drawer: null,
            tab: 0,
            fam: [[],[],[],[],[]],
            fam_fusion: [
                {
                    scr: "https://vignette.wikia.nocookie.net/bit-heroes/images/1/19/TreeToxicSlimeScreenshot.png/revision/latest/scale-to-width-down/200",
                    name: "Prof. Gak",
                    rarity: 1,
                    stats: {
                        power: 20.6,
                        health: 29.3,
                        speed: 15.1,
                    },
                    skills: [
                        [
                            "Attack (0SP)",
                            "Damage closest",
                            "90-110%"
                        ],
                        [
                            "Slime Cut (2SP)",
                            "Damage closest",
                            "183.2-274.8%"
                        ]
                    ],
                    where: [
                        {
                            scr: "https://vignette.wikia.nocookie.net/bit-heroes/images/f/f3/TreeScreenshot.png/revision/latest/scale-to-width-down/200",
                            name: "Prof. Oak",
                            rarity: 3,
                            stats: {
                                power: 19,
                                health: 27,
                                speed: 14,
                            },
                            skills: [
                                [
                                    "Attack (0SP)",
                                    "Damage closest",
                                    "90-110%"
                                ],
                                [
                                    "Paper Cut (2SP)",
                                    "Damage closest",
                                    "183.2-274.8%"
                                ]
                            ]
                        },
                        {
                            scr: "https://vignette.wikia.nocookie.net/bit-heroes/images/5/52/ToxicSlimeScreenshot.png/revision/latest/scale-to-width-down/200",
                            name: "Gak",
                            rarity: 1,
                            stats: {
                                power: 26,
                                health: 12,
                                speed: 22,
                            },
                            skills: [
                                [
                                    "Attack (0SP)",
                                    "Damage closest",
                                    "90-110%"
                                ],
                                [
                                    "Toxic Sludge (2SP)",
                                    "Ntcnbasd",
                                    "test%"
                                ],
                                [
                                    "Третий скилл",
                                    "Убивает всех",
                                    "test 123%"
                                ]
                            ]
                        },
                    ]
                }
            ]
        }),
        methods: {
            GetFams(){

            },
            getRarity(v) {
                if (v === 0) {
                    return ["green--text", "Обычные"]
                } else if (v === 1) {
                    return ["blue--text", "Редкие"]
                } else if (v === 2) {
                    return ["red--text", "Эпические"]
                } else if (v === 3) {
                    return ["yellow--text", "Легендарные"]
                } else if (v === 4) {
                    return ["pink--text", "Мифические"]
                } else {
                    return ["green--text", "Обычные ???"]
                }
            }
        },
        mounted() {
            this.fam = require("../assets/fams");
        },
        created() {
            this.$vuetify.theme.dark = false
        },
    }
</script>

